@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  background-color: #9ccc65;
}

.nav-link {
  float: left;
  color: black;
  background-color: #9ccc65;
  text-align: center;
  padding: 24px 5px;
  text-decoration: none;
  font-size: 15px;
  

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: #9ccc65;
}

.nav-right {
  float: right;
}
.toggle-button {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 2px;
    background: black;
}


.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #9ccc65;
  height: 56px;
}

.toolbar__navigation {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 1rem;
}

.toolbar__logo {
  margin-left: 1rem;
}



.spacer {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
}

.toolbar_navigation-items li {
  padding: 0 0.5rem;
}

.toolbar_navigation-items a {
  color: black;
  text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: #255c3e;
}

@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }

  .toolbar__logo {
    margin-left: 0;
  }
}

.side-drawer {
  height: 100%;
  background: #7cb342;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0);
  position: fixed;
  top: 0;
  left: 0;
  width: 50%;
  max-width: 200px;
  z-index: 200;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 1rem;
 
}

.side-drawer li {
  margin-top: 1.5rem;
}

.side-drawer a {
  color: black;
  text-decoration: none;
  font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: #255c3e;
}

.logOutButton {
  background: transparent;
  outline: none;
  border: 0;
  cursor: pointer;
}

.logOutButton a:hover,
.logOutButton a:active {
  color: #255c3e;
}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}

.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.7);
    z-index: 100;
}
footer {
    margin-top: 80px;
    position:absolute;
    bottom:0;
    width:100%;
    height:60px;   /* Height of the footer */
    background-color: #9ccc65;
}



body {
  font-family: 'Roboto Mono', monospace;
  margin: 0px;
  background-color: #9ccc65;
  text-align: center;
}

.alert {
  text-align: center;
  margin: 50px;
}

form {
  background-color: #c5e1a5;
  width: 60%;
  height: 250px;
  margin: 20px auto;
  padding: 25px;
  border-radius: 2px;
}

input {
  margin: 5px;
  margin-bottom: 10px;
  font-family: 'Roboto Mono', monospace;
  width: 75%;
}

.log-in, .log-out, .register {
  background-color: #7cb342;
  border-radius: 50%;
  color: #f8f8f8;
  min-height: 50px;
  width: 50px;
  outline:0;
  cursor: pointer;
  padding: 1px;
  border-radius: 50px;
  box-shadow: 0 4px rgb(65, 75, 68);
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #9ccc65;
}

.log-in:active {
  background-color: #33691e;
  box-shadow: 0 3px rgb(51, 50, 50);
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
  background:none;
  color:inherit;
  border:none;
  padding:0;
  font: inherit;
  outline:0;
  border-bottom:1px solid #444; 
  cursor: pointer;
}

img {
  height: 100px;
  margin: 20px;
  border-radius: 50px; 
}

.doge {
  box-shadow: 2px 2px 4px #000000;
}

.icon {
  padding: 0px;
  margin: 0px;
  height: 30px;
  outline: 0 none;
  border-radius: 0px;
}

.miniIcon {
  padding: 0px;
  margin: 0px;
  height: 20px;
  outline: 0 none;
  border-radius: 0px;
}

button {
  font-family: 'Roboto Mono', monospace;
  font-size: 18px;
  text-align: center;
  margin: 0px;
  padding: 0px;
}

a {
  color: #f1f8e9;
}

h2 {
  margin: 0px;
}

table {
  font-family: 'Roboto Mono', monospace;
  border-collapse: collapse;
  width: 100%;
  font-size: 16px;
}

td, th {
  border: 1px solid #dcedc8;
  text-align: center;
  padding: 6px;
}

td {
  padding: 4px;
}

tr:nth-child(even) {
  background-color: #dcedc8;
}

#map {
  height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
}

.container {
  display: -webkit-flex;
  display: flex;
 -webkit-justify-content: center;
         justify-content: center;
}

.bathroom {
  margin: 7px;
}

.legs {
  margin-right: 0;
}

.message {
  border-radius:0px;
}

.title {
  font-size: 50px;
  font-weight: bold;
}

.copyright {
  font-size: .8rem;
}

.sideBarDoge {
  border-radius:0px;
  height: 50px;
}
